.pagination {
  height: 60px;
  display: none;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  text-overflow: hidden;
}

.pagination span {
  padding: 8px;
  cursor: pointer;
}

.pagination .active {
  background-color: #f46a2a;
  border-radius: 4px;
  user-select: none;
}

.pagination svg {
  width: 14px;
}

@media only screen and (min-width: 768px) {
  .pagination {
    display: flex;
  }
}
